@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500,500i,700,700i,900,900i");
* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

@media (min-width: 3000px) {
  html {
    font-size: 32px;
  }
}
.watch-body {
  color: #bacbd8;
  background-color: #000;
  font-family: Roboto, Helvetica, Arial, sans-serif;
}
.watch-body .testing-buttons {
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 1rem;
  z-index: 2;
}
.watch-body .testing-buttons a {
  color: #00cbed;
}
.watch-body .testing-buttons a:hover, .watch-body .testing-buttons a:focus {
  color: #5ee8ff;
}
.watch-body .testing-buttons a:active {
  color: #0078aa;
}

.watch {
  width: 45.833333vw;
  height: 45.833333vw;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -22.916667vw 0 0 -22.916667vw;
}
.watch.hidden {
  visibility: hidden;
}
.watch .tick {
  fill: #333;
}
.watch .tick-active {
  fill: #bacbd8;
}
.watch .class {
  position: absolute;
  left: 8.333333vw;
  top: 18.854167vw;
  width: 29.166667vw;
  height: 8.125vw;
  text-align: center;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
}
.watch .class::before {
  content: "";
  display: inline-block;
  height: 8.125vw;
  vertical-align: middle;
}
.watch .class-text {
  display: inline-block;
  vertical-align: middle;
  width: 28.645833vw;
  font-size: 3.75vw;
  line-height: 4.0625vw;
}
.watch .timer {
  position: absolute;
  width: 15.625vw;
  height: 6.041667vw;
  left: 15.104167vw;
  top: 28.645833vw;
  font-size: 6.25vw;
  line-height: 0.75;
  text-align: center;
}
.watch .minutes,
.watch .seconds {
  position: relative;
}
.watch .minutes::before,
.watch .seconds::before {
  position: absolute;
  left: 0.3125vw;
  bottom: 0;
  color: #738ea3;
  font-size: 1.25vw;
  font-weight: normal;
}
.watch .minutes {
  font-weight: bold;
}
.watch .minutes::before {
  content: "MIN";
}
.watch .seconds {
  font-weight: 100;
}
.watch .seconds::before {
  content: "SEC";
}
.watch.inactive {
  color: #333;
}
.watch.inactive .minutes::before,
.watch.inactive .seconds::before {
  color: inherit;
}

[class^=brand-] .class,
[class*=" brand-"] .class {
  left: 9.270833vw;
  width: 27.291667vw;
}
[class^=brand-] .class-text,
[class*=" brand-"] .class-text {
  display: none;
}

